<template>
<div class="row">
  <label class="col col-form-label">prix</label>
  <div class="col-3">
    <input type="number"
      class="form-control"
      v-model="current.amount"
      @input="$emit('update:price', price, current)">
  </div>
  <label class="col col-form-label">quantité</label>
  <div class="col-2">
    <input type="number"
      class="form-control"
      min="1"
      v-model="current.quantity"
      @input="$emit('update:price', price, current)">
  </div>
  <label class="col col-form-label">unité/pièces</label>
  <div class="col-2">
    <input type="text"
      class="form-control"
      v-model="current.unit"
      list="units"
      @input="$emit('update:price', price, current)">
  </div>
  <div class="col">
    <div class="btn-group ms-auto" role="group">
      <button v-if="create" type="button"
        class="btn btn-outline-primary"
        :disabled="!enabled"
        @click="emitAdd">
        <i class="bi bi-plus"></i>
      </button>
      <button v-else type="button"
      class="btn btn-outline-danger"
      title="supprimer la ligne courante"
      :disabled="!enabled"
      @click="$emit('delete:price', price)">
        <i class="bi bi-dash"></i>
      </button>
    </div>
  </div>
</div>
</template>
<script>

export default {
  name: 'ProductPriceEdit',
  props: {
    price: Object,
    create: {
      type: Boolean,
      default: false,
    }
  },
  emits: ['update:price', 'delete:price', 'add:price'],
  data() {
    return {
      current: Object.assign({}, this.price)
    }
  },
  computed: {
    enabled() {
      return this.current.amount > 0 && this.current.quantity > 0 && this.current.unit != ""
    }
  },
  methods: {
    emitAdd() {
      this.$emit('add:price', this.current)
      this.current = Object.assign({}, this.price)
    }
  },
}
</script>
