<template>
  <Modal ref="modal" :title="title" @modal:close='done' :action="submit">
    <template v-slot:default>
      <form>
        <fieldset>
          <div class="mb-3">
            <label class="form-label">produit</label>
            <input type="text" class="form-control" v-model="product.name">
          </div>
          <div class="mb-3">
            <label class="form-label">description</label>
            <input type="text" class="form-control" v-model="product.desc">
          </div>
          <div class="mb-3">
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="checkbox" v-model="product.highlight">
              <label class="form-check-label">mettre en avant</label>
            </div>
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="checkbox" v-model="product.available">
              <label class="form-check-label">disponible?</label>
            </div>
          </div>
        </fieldset>
        <fieldset>
          <legend class="fs-4">Categorie(s)</legend>
          <datalist id="categories">
            <option v-for="c in categories" :key="c" :value="c">{{c}}</option>
          </datalist>
          <div class="mb-3" v-for="(c, i) in product.categories" :key="i">
            <ProductCategoryEdit v-model:category="product.categories[i]"
              @update:category="upCategory"
              @delete:category="remCategory"/>
          </div>
          <div class="mb-3">
            <ProductCategoryEdit v-model:category="category"
              :create="true"
              @add:category="addCategory"/>
          </div>
        </fieldset>
        <fieldset>
          <legend class="fs-4">Tarif(s)</legend>
          <datalist id="units">
            <option v-for="u in units" :key="u" :value="u">{{u}}</option>
          </datalist>
          <div class="mb-3" v-for="(p, i) in product.prices" :key="i">
            <ProductPriceEdit v-model:price="product.prices[i]"
              @delete:price="remPrice"
              @update:price="upPrice"/>
          </div>
          <div class="mb-3">
            <ProductPriceEdit v-model:price="price"
              :create="true"
              @add:price="addPrice"/>
          </div>
        </fieldset>
      </form>
    </template>
    <template v-slot:action>
      <button type="button" class="btn btn-primary">sauver</button>
    </template>
  </Modal>
</template>
<script>
import {mapGetters} from 'vuex'
import _ from 'lodash'
import Modal from '../components/Modal.vue'
import ProductPriceEdit from '../components/ProductPriceEdit.vue'
import ProductCategoryEdit from '../components/ProductCategoryEdit.vue'

const model = {
  name: "",
  desc: "",
  prices: [],
  categories: [],
  available: false,
  highlight: false,
}

export default {
  name: 'EditProduct',
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.resetDefault()
      vm.toggle()
    })
  },
  data() {
    return {
      product: {},
      category: "",
      price: {quantity: 0, unit: "", amount: 0},
    }
  },
  computed: {
    ...mapGetters('catalog', ['products', 'categories', 'units']),
    editMode() {
      return this.$route.name == 'edit.product'
    },
    title() {
      return this.editMode ? `détail de "${this.product.name}"` : 'créer un nouveau produit'
    }
  },
  methods: {
    toggle() {
      this.$refs.modal.toggle()
    },
    done() {
      this.$router.go(-1)
    },
    resetDefault() {
      if (!this.$route.params.id) {
        this.product = Object.assign({}, model)
        return
      }
      this.product = _.cloneDeep(this.products[this.$route.params.id])
    },
    addCategory(category) {
      if (!category) {
        return
      }
      let x = this.product.categories.indexOf(category)
      if (x >= 0) {
        return
      }
      this.product.categories.push(category)
    },
    remCategory(category) {
      let x = this.product.categories.indexOf(category)
      if (x < 0) {
        return
      }
      this.product.categories.splice(x, 1)
    },
    upCategory(category, cat) {
      let x = this.product.categories.indexOf(category)
      if (x < 0) {
        return
      }
      this.product.categories.splice(x, 1, cat)
    },
    addPrice(price) {
      this.product.prices.push(price)
    },
    remPrice(price) {
      let x = this.product.prices.findIndex(p => _.isEqual(p, price))
      if (x < 0) {
        return
      }
      this.product.prices.splice(x, 1)
    },
    upPrice(old, price) {
      let x = this.product.prices.findIndex(p => _.isEqual(p, old))
      if (x < 0) {
        return
      }
      this.product.prices.splice(x, 1, price)
    },
    submit() {

    },
  },
  components: {
    Modal,
    ProductPriceEdit,
    ProductCategoryEdit,
  },
}
</script>
