import catalog from './sample.js'
import _ from 'lodash'

const state = {
  products: catalog,
}

const getters = {
  products: (state) => state.products,
  categories: (state) => {
    return _.uniq(state.products.flatMap(p => p.categories))
  },
  units: () => {
    return _.uniq(state.products.flatMap(p => p.prices.map(p => p.unit)))
  }
}

const mutations = {}

const actions = {}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
}
