<template>
  <div class="row">
    <label class="col col-form-label">nom</label>
    <div class="col-10">
      <input type="text"
        class="form-control"
        v-model="current"
        list="categories"
        @input="$emit('update:category', category, $event.target.value)">
      <datalist id="categories">
        <option v-for="c in categories" :key="c" :value="c">{{c}}</option>
      </datalist>
    </div>
    <div class="col">
      <div class="btn-group ms-auto" role="group">
        <button v-if="create" type="button"
          class="btn btn-outline-primary"
          :disabled='!current.length'
          @click="emitAdd">
          <i class="bi bi-plus"></i>
        </button>
        <button v-else type="button"
        class="btn btn-outline-danger"
        title="supprimer la ligne courante"
        :disabled='!current.length'
        @click="$emit('delete:category', category)">
          <i class="bi bi-dash"></i>
        </button>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'ProductCategoryEdit',
  props: {
    category: String,
    create: {
      type: Boolean,
      default: false,
    }
  },
  emits: ['update:category', 'delete:category', 'add:category'],
  data() {
    return {
      current: this.category,
    }
  },
  methods: {
    emitAdd() {
      this.$emit('add:category', this.current)
      this.current = ""
    },
  },
}
</script>
